	import TabGroup from '../group/tab'
	import {getMyProfitList} from '@/api/deal'
	export default{
		data(){
			return{
               title:"我的收益",
               params:{
               	 pageNum:1,
               	 pageSize:10,
               	 userId:this.$store.getters.userInfo.userId
               },
               incomeList:null

			}
		},
		mounted(){
           this.getMyProfitList();
		},
	    filters:{
         momment(a){
          let b="";
      b=new Date(a).getFullYear()+'-'+new Date(a).getMonth()+'-'+new Date(a).getDate()+' '+new Date(a).getHours()+':'+new Date(a).getMinutes()+':'+new Date(a).getSeconds();
      return b;
         }
		},
		components:{
         TabGroup
		},
		methods:{
			getMyProfitList(){
				getMyProfitList(this.params).then(res=>{
					console.log(res);
					this.incomeList=res.data;
				})
			}

		}
	}
	



